import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import {
  Form,
  Breadcrumb,
  Tabs,
  Table,
  Popconfirm,
  Tooltip,
  Button,
  Modal,
  Row,
  Col,
  Input,
  InputNumber,
  message,
} from 'antd';
import styled from 'styled-components';
import { injectIntl } from 'react-intl';

import {
  getNodes,
  addNode,
  deleteNode,
  restartNode,
  enabledNode,
  disabledNode,
} from './../../services';

const { TabPane } = Tabs;
const FormItem = Form.Item;
const ButtonAction = styled(Button)`
  margin: 5px;
`;

const NodesCmp = props => {
  const {
    form,
    form: { getFieldDecorator },
    intl: { messages },
  } = props;

  const [visible, setVisible] = useState(false);
  const [visibleEdit, setVisibleEdit] = useState(false);
  const [mode, setMode] = useState('add');
  const [loading, setLoading] = useState(true);
  const [nodes, setNodes] = useState([]);
  // eslint-disable-next-line no-unused-vars
  const [current, setCurrent] = useState({
    name: '',
    ipaddress: '',
    port: null,
    status: null,
  });

  useEffect(() => {
    fetchNodes();
  }, []);

  const fetchNodes = async () => {
    try {
      const response = await getNodes();
      const {
        data: { nodes },
      } = response;
      setNodes(nodes);
      setLoading(false);
    } catch (error) {
      message.error(error.message);
    }
  };

  const delNode = async node => {
    try {
      setLoading(true);
      await deleteNode({ id: node.id });
      message.success(messages.default.success);
      fetchNodes();
    } catch (error) {
      message.error(error.message);
    }
  };

  const resNode = async node => {
    try {
      setLoading(true);
      await restartNode({ id: node.id });
      message.success(messages.default.success);
      fetchNodes();
    } catch (error) {
      message.error(error.message);
    }
  };
  const enbNode = async node => {
    try {
      setLoading(true);
      await enabledNode({ id: node.id });
      message.success(messages.default.success);
      fetchNodes();
    } catch (error) {
      message.error(error.message);
    }
  };
  const disNode = async node => {
    try {
      setLoading(true);
      await disabledNode({ id: node.id });
      message.success(messages.default.success);
      fetchNodes();
    } catch (error) {
      message.error(error.message);
    }
  };

  const handleReset = () => {
    form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          id: current.id || null,
          name: values.name,
          ipaddress: values.ipaddress,
          port: values.port,
          // status: values.status,
        };
        setLoading(true);

        if (mode === 'add') {
          try {
            await addNode(form);
            message.success(messages.default.success);
            handleReset();
            setVisible(false);
            fetchNodes();
          } catch (error) {
            message.error(error.message);
          }
        }
      }
    });
  };

  const columns = [
    {
      title: messages.nodes.name,
      dataIndex: 'name',
      key: 'name',
      render: (text, record) => (
        <span>
          <strong>({record.id})</strong> {text}
        </span>
      ),
    },
    {
      title: messages.nodes.ipaddress,
      dataIndex: 'ipaddress',
      key: 'ipaddress',
    },
    {
      title: messages.nodes.port,
      dataIndex: 'port',
      key: 'port',
    },
    {
      title: messages.nodes.status,
      dataIndex: 'status',
      key: 'status',
      render: (text, record) => {
        const status = [
          'STATUS_READY',
          'STATUS_RUNNING',
          'STATUS_STARTING',
          'STATUS_STOPPING',
          'STATUS_SYNC',
          'STATUS_FULL',
          'STATUS_RESTARTING',
          'STATUS_REFRESHING',
          'STATUS_OFF',
          'STATUS_ERROR',
        ];
        if (text === -1) return status[9];
        return status[text];
      },
    },
    {
      title: messages.default.actions,
      key: 'action',
      width: 200,

      render: (text, record) => (
        <span>
          {record.status === 8 ? (
            <Tooltip placement="top" title={messages.nodes.enabled}>
              <ButtonAction
                onClick={() => {
                  enbNode(record);
                }}
                ghost
                size="small"
                type="danger"
                shape="circle"
                icon="stop"
              />
            </Tooltip>
          ) : (
            <Tooltip placement="top" title={messages.nodes.disabled}>
              <ButtonAction
                onClick={() => {
                  disNode(record);
                }}
                ghost
                size="small"
                type="primary"
                shape="circle"
                icon="check"
              />
            </Tooltip>
          )}
          <Tooltip placement="top" title={messages.nodes.eds}>
            <Link to={`/dashboard/eds/node/${record.id}`}>
              {' '}
              <ButtonAction ghost size="small" type="primary" shape="circle" icon="apartment" />
            </Link>
          </Tooltip>
          <Popconfirm
            onConfirm={() => {
              resNode(record);
            }}
            title={messages.nodes.confirmRestart}
            okText={messages.default.yes}
            cancelText={messages.default.no}
          >
            <Tooltip placement="top" title={messages.nodes.restart}>
              <ButtonAction ghost size="small" type="primary" shape="circle" icon="reload" />
            </Tooltip>
          </Popconfirm>
          <Popconfirm
            onConfirm={() => {
              delNode(record);
            }}
            title={messages.nodes.confirmDelete}
            okText={messages.default.yes}
            cancelText={messages.default.no}
          >
            <Tooltip placement="top" title={messages.nodes.delete}>
              <ButtonAction size="small" type="danger" shape="circle" icon="delete" />
            </Tooltip>
          </Popconfirm>
        </span>
      ),
    },
  ];

  const operations = (
    <Button
      onClick={() => {
        setVisible(true);
        setMode('add');
      }}
      type="primary"
      icon="deployment-unit"
    >
      {messages.nodes.add}
    </Button>
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  return (
    <div>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>{messages.menus.nodes}</Breadcrumb.Item>
      </Breadcrumb>
      <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
        <Tabs defaultActiveKey="all" tabBarExtraContent={operations}>
          <TabPane tab={messages.nodes.all} key="all">
            <Table size="small" loading={loading} columns={columns} dataSource={nodes}  rowKey={record => record.id}/>
          </TabPane>
        </Tabs>

        <Modal
          centered
          width={600}
          title={messages.nodes.add}
          visible={visible}
          onCancel={() => {
            setVisible(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisible(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.create}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.nodes.name} hasFeedback>
                  {getFieldDecorator('name', {
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.name.required,
                      },
                    ],
                  })(<Input type="text" />)}
                </FormItem>
                <FormItem label={messages.nodes.ipaddress} hasFeedback>
                  {getFieldDecorator('ipaddress', {
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.ipaddress.required,
                      },
                    ],
                  })(<Input type="text" />)}
                </FormItem>
                {/* <FormItem label={messages.nodes.status} hasFeedback>
                  {getFieldDecorator('status', {
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.status.required,
                      },
                    ],
                  })(
                    <Select>
                      <Option value={0}>STATUS_READY</Option>
                      <Option value={1}>STATUS_RUNNING</Option>
                      <Option value={2}>STATUS_STARTING</Option>
                      <Option value={3}>STATUS_STOPPING</Option>
                      <Option value={4}>STATUS_SYNC</Option>
                      <Option value={5}>STATUS_FULL</Option>
                      <Option value={6}>STATUS_RESTARTING</Option>
                      <Option value={7}>STATUS_REFRESHING</Option>
                      <Option value={8}>STATUS_OFF</Option>
                      <Option value={-1}>STATUS_ERROR</Option>
                    </Select>
                  )}
                </FormItem> */}
                <FormItem label={messages.nodes.port} hasFeedback>
                  {getFieldDecorator('port', {
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.port.required,
                      },
                    ],
                  })(<InputNumber />)}
                </FormItem>
              </Col>
            </Row>
          </Form>
        </Modal>
        <Modal
          centered
          width={600}
          title={messages.nodes.edit}
          visible={visibleEdit}
          onCancel={() => {
            setVisibleEdit(false);
          }}
          footer={[
            <Button
              key="back"
              onClick={() => {
                setVisibleEdit(false);
              }}
            >
              {messages.default.cancel}
            </Button>,
            <Button key="submit" type="primary" loading={loading} onClick={handleSubmit}>
              {messages.default.save}
            </Button>,
          ]}
        >
          <Form {...formItemLayout}>
            <Row justify="center" type="flex">
              <Col xs={24}>
                <FormItem label={messages.nodes.name} hasFeedback>
                  {getFieldDecorator('name', {
                    initialValue: current.name,
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.name.required,
                      },
                    ],
                  })(<Input type="text" />)}
                </FormItem>
                <FormItem label={messages.nodes.ipaddress} hasFeedback>
                  {getFieldDecorator('ipaddress', {
                    initialValue: current.ipaddress,
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.ipaddress.required,
                      },
                    ],
                  })(<Input />)}
                </FormItem>
                <FormItem label={messages.nodes.port} hasFeedback>
                  {getFieldDecorator('port', {
                    initialValue: current.port,
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.port.required,
                      },
                    ],
                  })(<InputNumber />)}
                </FormItem>
                {/* <FormItem label={messages.nodes.status} hasFeedback>
                  {getFieldDecorator('status', {
                    initialValue: current.status,
                    rules: [
                      {
                        required: true,
                        message: messages.nodes.rules.status.required,
                      },
                    ],
                  })(<InputNumber />)}
                </FormItem> */}
              </Col>
            </Row>
          </Form>
        </Modal>
      </div>
    </div>
  );
};
const WrappedNodesCmp = Form.create()(injectIntl(NodesCmp));

export default WrappedNodesCmp;
